@import '../../styles/utils';

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container {
  width: 100%;
  text-align: center;
}

.item {
  width: 48px;
  height: 48px;
  border: 5px solid $color-elements;
  border-bottom-color: $color-primary;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
